<template>
    <div class="box box-primary">
        <div class="box-header with-border">
            <h3 class="box-title">Các sản phẩm đã lưu</h3>
            <div class="row product-controller">
                <div class="col-xs-8">
                    <Select2 v-model="id_category" 
                    :options="categories(false)" 
                    @change="changeCategory($event)" />
                </div>
                <div class="col-xs-4">
                    <Select2 v-model="pageSize" 
                    :options="[{id:10, text: '10sp/Trang'}, {id:20, text: '20sp/Trang'}, {id:50, text: '50sp/Trang'}]" 
                    @change="changePageSize($event)" />
                </div>
            </div>
            <div class="input-group user-product-search">
                <input v-on:keyup="search" type="text" class="form-control" placeholder="Tìm kiếm... ">
                <span class="input-group-addon" title="Tìm"><i class="fa fa-search"></i></span>
            </div>
        </div>
        <div class="box-body no-padding" id="_user-product">
            <div v-for="(item, index) in product" :key="index" class="item">
                <div class="img">
                    <img  :src="api + '/image/product/thumbnail/'+item.image" :alt="item.name">
                    <span class="status">
                        {{ getTime(item.date).fromNow() }}
                    </span>
                </div>
                <div class="about">
                    <h2 class="name">{{ item.name }}</h2>
                    <div class="bottom">
                        <h3 class="category">{{ item.category }}</h3>
                        <h3 v-if="item.price!=0" class="price">{{ number_format(item.price,0) }} đ</h3>
                        <h3 v-else class="price">Thoả thuận</h3>
                    </div>
                </div>
                <div class="controller">
                    <router-link :to="{name:'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id_product, product: item.url }}" class="btn btn-success">Xem ngay</router-link>
                    <a v-on:click="deleteProductFolow(index)" class="btn btn-danger">Xoá</a>
                </div>
            </div>
            <paginate v-model="page"
                :page-count="pageCount"
                :page-range="3"
                :margin-pages="2"                                           
                :click-handler="runPage"
                :prev-text="`<i class='fa fa-angle-left' aria-hidden='true'></i>`"
                :next-text="`<i class='fa fa-angle-right' aria-hidden='true'></i>`"
                :container-class="'pagination'"
                :page-class="'page-item'">
            </paginate>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import {mapGetters} from "vuex";
import axios from "axios";
import Select2 from "v-select2-component";
import paginate from "vuejs-paginate";

export default {
    metaInfo(){
		return {
			title: "Các tin đã lưu",
			htmlAttrs: {
				lang: 'vi',
				amp: true
			}
		}
	},
    components:{Select2, paginate},
    data(){
        return {
            api: this.$store.state.domainApi,
            id_category: 'tat-ca-danh-muc',
            productStatus: 1,
            page:1,
            pageSize:20,
            pageCount:0,
            product:[]
        }
    },
    methods:{
        getTime(date){
            var time = new moment(date);
            return time;
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        changeCategory(){
            this.page = 1;
            this.getData();
        },
        changePageSize(size){
            this.pageSize = size;
            this.page = 1;
            this.getData();
        },
        runPage(page){
            this.page = page;
            this.getData();
        },
        search(e){
            this.page = 1;
            this.getData(e.target.value); 
        },
        getData(search=''){
            axios.get(this.api + "/auth/search-product-follow", {
                params:{
                    id_user: this.$store.state.user.id,
                    id_category: this.id_category,
                    search: search,
                    page: this.page,
                    pageSize: this.pageSize
                }
            })
            .then(response=>{
                this.product = response.data.product;
                this.pageCount = Math.ceil(response.data.count/this.pageSize);
            })
            .catch(error=>{
                console.log(error);
            });
        },
        deleteProductFolow(index){
            this.$swal.fire({
                title: 'Xác nhận xoá tin này !',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không!',
                confirmButtonText: 'Xóa!'
            }).then((result) => {
                if (result.value) {
                    axios.delete(this.api + "/auth/product-follow",{
                        headers:{
                            Authorization: `Bearer ${this.$store.state.token}`
                        },
                        params:{
                            id_product: this.product[index].id_product
                        }
                    })
                    .then(response=>{
                        if(response.data.success){
                            this.$store.state.countProductFollow--;
                            var key = this.$store.state.user.productFollow.findIndex(e=>e.id_product == this.product[index].id_product);
                            this.$store.state.user.productFollow.splice(key,1);
                            
                            this.product.splice(index,1);
                            this.flash(response.data.message, "success", { timeout: 3000});
                        }else this.flash(response.data.message, "error", { timeout: 3000});
                    });
                }
            }); 
        }
    },
    computed:{
        ...mapGetters({user: 'getUser', categories: 'getCategorySort'})
    },
    created(){
        moment.locale("vi");
        this.getData();
    }
}
</script>

